import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Network } from '@ionic-native/network';
import { Storage } from '@ionic/storage';
import { AlertController, LoadingController, ModalController } from 'ionic-angular';
import { SocialSharing } from '@ionic-native/social-sharing';
import { Toast } from '@ionic-native/toast';

import moment from 'moment';

@Injectable()

export class SermonsProvider {
  sermon: any;
  progress: any = 0;
  isDownloaded: Boolean = false;

  modal: any;
  user: any;

  hasValidVoucher: boolean = false;

  constructor(public http: HttpClient, private network: Network, public storage: Storage, private socialSharing: SocialSharing, public loadingCtrl: LoadingController, public alertCtrl: AlertController, public toast: Toast, private modalCtrl: ModalController) {
    this.storage.get('user').then(user => {
      this.user = user;
    });
  }

  init() {
    console.log('precheck', this.checkUser())
    // this.openModal();
  }

  checkUser() {
    // this.storage.get('user').then(user =>  user );
    const status = this.storage.get('user').then(user => {
      return user;
    });
    return status;
  }

  openModal(status = null) {
   if (status === 'expired') {
    this.modal = this.modalCtrl.create('VoucherCheckPage', { expired: true });
    this.modal.present();
   } else {
    this.modal = this.modalCtrl.create('VoucherCheckPage');
    this.modal.present();
   }
  }

  verifyVoucher() {
    console.log(this.checkUser())
    // if (this.checkUser() !== null) {
    //   if (!this.user.sub_active) {
    //     this.openModal();
    //   }
    //   if (this.user.sub_active && moment(this.user.sub_end).isBefore(moment())) {
    //     this.openModal('expired')
    //   }
    // } else {
    //   this.openModal();
    // }
  }

  preDownloadCheck() {
    if (this.network.type == 'wifi') {
      // this.downloadSermon();
    } else if (this.network.type === 'none') {
      const alert = this.alertCtrl.create({
        title: 'No Internet connection',
        subTitle: 'You need an internet connection to download sermons.',
        buttons: ['OK']
      });
      alert.present();
    } else {
      const confirm = this.alertCtrl.create({
        title: 'Download without WiFi?',
        message: 'You are not connected to a WiFi, you sure you want to download this sermon using your data connection?',
        buttons: [
          {
            text: 'Cancel',
            handler: () => {
              console.log('Disagree clicked');
            }
          },
          {
            text: 'Yes, please',
            handler: () => {
              // this.downloadSermon();
            }
          }
        ]
      });
      confirm.present();
    }
  }

  validateVoucher(code) {
    console.log('voucher', code);
    let loading = this.loadingCtrl.create({
      content: 'Verifying voucher...',
      duration: 4000
    });
    loading.present();
    // this.downloadSermon();
  }

  shareSermon(sermon) {
    this.socialSharing.share(sermon.title, '', sermon.audio, 'market url').then(_ => {});
  }

}



