import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { Geolocation } from '@ionic-native/geolocation';

@Injectable()
export class LocationsProvider {
  data: any;
  userLocation: any

  constructor(public http: HttpClient, private geolocation: Geolocation) {}

  load() {
    if (this.data) {
      return Promise.resolve(this.data);
    }

    return new Promise(resolve => {
      this.geolocation.getCurrentPosition().then((resp) => {
        const location = { lat: resp.coords.latitude, lng: resp.coords.longitude };
        this.userLocation = location;
        this.http.get('assets/data/locations.json').subscribe(data => {
          this.data = this.applyHaversine(data['centers']);
          this.data.sort((locationA, locationB) => {
            return locationA.distance - locationB.distance;
          });
          resolve(this.data);
        });
      }).catch((error) => {
         console.log('Error getting location', error);
      });
    });
  }

  applyHaversine(locations) {
    locations.map((location) => {
      let placeLocation = {
        lat: location.lat,
        lng: location.lon
      };
      location.distance = this.getDistanceBetweenPoints(this.userLocation, placeLocation, 'km').toFixed(0);
    });

    return locations;
  }

  getDistanceBetweenPoints(start, end, units){
    let earthRadius = {
      miles: 3958.8,
      km: 6371
    };

    let R = earthRadius[units || 'miles'];
    let lat1 = start.lat;
    let lon1 = start.lng;
    let lat2 = end.lat;
    let lon2 = end.lng;

    let dLat = this.toRad((lat2 - lat1));
    let dLon = this.toRad((lon2 - lon1));
    let a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(this.toRad(lat1)) * Math.cos(this.toRad(lat2)) *
    Math.sin(dLon / 2) *
    Math.sin(dLon / 2);
    let c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    let d = Math.floor(R * c);

    return d;
  }

  toRad(x){
    return x * Math.PI / 180;
  }

}



