import { Geolocation } from "@ionic-native/geolocation";
import { Toast } from "@ionic-native/toast";
import { HttpClient } from "@angular/common/http";
import { Component, ViewChild } from "@angular/core";
import { Nav, Platform, AlertController, App } from "ionic-angular";
import { StatusBar } from "@ionic-native/status-bar";
import { SplashScreen } from "@ionic-native/splash-screen";
import { Storage } from "@ionic/storage";
import { OneSignal } from "@ionic-native/oneSignal";
import { Network } from "@ionic-native/network";
import { Mixpanel } from "@ionic-native/mixpanel";

@Component({
  templateUrl: "app.html"
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  rootPage: String;

  constructor(
    public app: App,
    platform: Platform,
    public statusBar: StatusBar,
    splashScreen: SplashScreen,
    public storage: Storage,
    public alertCtrl: AlertController,
    public http: HttpClient,
    private oneSignal: OneSignal,
    private network: Network,
    private toast: Toast,
    private geolocation: Geolocation,
    private mixpanel: Mixpanel
  ) {
    platform.ready().then(() => {
      // statusBar.styleDefault();
      this.statusBar.overlaysWebView(true);
      this.statusBar.hide();
      this.oneSignalInit();
      this.grabData();
      this.initPages();
      this.initLocation();
      splashScreen.hide();
    });

    // watch network for a disconnect
    let disconnectSubscription = this.network.onDisconnect().subscribe(() => {
      this.toast.showLongBottom("Network was disconnected :-(");
    });

    // stop disconnect watch
    disconnectSubscription.unsubscribe();

    // watch network for a connection
    // let connectSubscription = this.network.onConnect().subscribe(() => {
    this.network.onConnect().subscribe(() => {
      setTimeout(() => {
        if (this.network.type !== "none") {
          this.grabData();
        }
      }, 3000);
    });
  }

  initPages() {
    this.mixpanel.track("App Opened");
    this.storage.ready().then(() => {
      this.storage.get("user").then(value => {
        if (value === null) {
          this.rootPage = "LandingPage";
        } else {
          this.rootPage = "TabsPage";
        }
      });
    });
    this.mixpanelInit();
  }

  grabData() {
    this.storage.get("sermons").then(sermons => {
      if (sermons === null) {
        if (this.network.type === "none") {
          let alert = this.alertCtrl.create({
            title: "No Internet Connection",
            subTitle: "Kindly check your internet connection.",
            buttons: ["OK"]
          });
          alert.present();
        } else {
          this.http
            .get("https://realm-glory.herokuapp.com/api/sermons")
            .subscribe(sermons => {
              this.storage.set("sermons", sermons["data"]);
            });
        }
      }
    });
  }

  mixpanelInit() {
    this.mixpanel
      .init("402e59f2f20f30cc73da554069bfffe8")
      .then(status => {
        console.log("success", status);
      })
      .catch(err => {
        console.log("err", err);
      });
  }

  oneSignalInit() {
    this.oneSignal.startInit(
      "d9b7eddc-f5b2-4cdc-a295-b415f9c40674",
      "590919248274"
    );
    this.oneSignal.inFocusDisplaying(
      this.oneSignal.OSInFocusDisplayOption.InAppAlert
    );
    this.oneSignal.iOSSettings({
      kOSSettingsKeyAutoPrompt: true,
      kOSSettingsKeyInAppLaunchURL: false
    });

    // do something when notification is received
    this.oneSignal.handleNotificationReceived().subscribe(data => {});

    // do something when a notification is opened
    this.oneSignal.handleNotificationOpened().subscribe(data => {
      // console.log("pushed v2", data);
      if (data.notification.payload.additionalData.sermon) {
        this.nav.setRoot("TabsPage");
      } else {
        let alert = this.alertCtrl.create({
          title: "New Notification!",
          subTitle: data.notification.payload.body,
          buttons: ["OK"]
        });
        alert.present();
      }
    });

    this.oneSignal.endInit();
  }

  initLocation() {
    this.geolocation
      .getCurrentPosition()
      .then(resp => {
        const location = {
          lat: resp.coords.latitude,
          long: resp.coords.longitude
        };
        this.storage.set("location", location);
      })
      .catch(error => {
        console.log("Error getting location", error);
      });
  }
}



